<template>
  <div class="list">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">测点配置</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import {getExportAuto} from '@/api/statistics'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        pointType: '',
        alias: '',
        itemName: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      typeList: [
        {name: '电压', value: 'VOLTAGE'},
        {name: '功率', value: 'POWER'},
        {name: '流量', value: 'FLOW'},
        {name: '温度', value: 'TEMPERATURE'},
        {name: '压力', value: 'PRESSURE'},
        {name: '电流', value: 'CURRENT'}
      ],
      tableData: [],
      maxlength: 50,
      tableColumns: [
        {
          title: '序号',
          key: 'id',
          align: 'left'
        },
        {
          title: '文件名称',
          key: 'fileName',
          align: 'left'
        },
        {
          title: '文件相对目录',
          key: 'fileUrl',
          align: 'left'
        },
        {
          title: '文件大小',
          key: 'fileSize',
          align: 'left'
        },
        {
          title: '生成时间',
          key: 'createTime',
          align: 'left',
          render: (h, params) => {
            return h('div', params.row.createTime.split('T')[0] + ' ' + params.row.createTime.split('T')[1])
          }
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.download(params.row)
                    }
                  }
                },
                '下载'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [
        {
          label: '测点名称',
          type: 'input',
          value: 'itemName',
          placeholder: '请输入测点名称'
        },
        {
          label: '测点类型',
          type: 'select',
          value: 'pointType',
          data: []
        },
        {
          label: '公式',
          type: 'textarea',
          value: 'formula',
          placeholder: '请输入公式'
        },
        {
          label: '测点描述',
          type: 'input',
          value: 'alias',
          placeholder: '请输入测点描述'
        }
      ],
      formValue: {},
      formValidate: {
        itemName: [{required: true, message: '测点名称不能为空'}],
        pointType: [{required: true, message: '测点类型不能为空'}],
        formula: [{required: true, message: '公式不能为空'}],
        alias: [{required: true, message: '测点描述不能为空'}]
      },
      type: ''
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getType () {
      this.formList[1].data = this.typeList
    },
    download (params) {
      let baseUrl = window.location.origin
      let path = '/device-batch-ai/download/'
      window.open(baseUrl + path + encodeURIComponent(params.fileName))
    },
    loadTableData () {
      let { pageSize, pageIndex: pageNumber } = this.pageInfo
      this.pageInfo.loading = true
      getExportAuto({pageSize, pageNumber}).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.result
          this.pageInfo.totalElements = res.data.totalElements
          this.pageInfo.loading = false
        } else {
          this.tableData = []
          this.pageInfo.totalElements = 0
          this.pageInfo.loading = false
        }
      })
    },
    showDrawer (type, params) {
      if (type !== 'delete') {
        if (type === 'add') {
          this.title = '新增自定义测点'
          this.formValue = {}
        } else {
          let val = JSON.parse(JSON.stringify(params))
          this.title = '编辑测点'
          this.formValue = val
        }
        this.type = type
        this.$refs.drawer.open()
      } else {
      }
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  }
}
</script>

<style lang="less" scoped>
@import '../../style/varient';
@size-padding: 10px;
.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



