import axiosDownLoad from '@/libs/api.download'

export const getExport = (params) => {
  return axiosDownLoad.request({
    url: '/record' + '?fileType=EXPORT&' + 'pageSize=' + params.pageSize + '&pageNumber=' + params.pageNumber,
    method: 'get'
  })
}

export const getExportAuto = (params) => {
  return axiosDownLoad.request({
    url: '/record' + '?fileType=BATCH&' + 'pageSize=' + params.pageSize + '&pageNumber=' + params.pageNumber,
    method: 'get'
  })
}



