<template>
  <div class="list">
    <div class="content">
      <div class="top-box">
        <div class="infoBox">
          <div class="title">寿命预测</div>
          <div class="box">
            <div class="line">
              <div class="item">
                <div>发动机四配套</div>
                <div>剩余寿命</div>
              </div>
              <div>
                <span style="font-size:20px">32</span>
                天
                <span style="font-size:20px">15</span>
                小时
              </div>
              <div style="color:#aaa">
                预测时间：2022-05-30 12:00:05
              </div>
            </div>
              <div class="line">
              <div class="item">
                <div>DPF系统</div>
                <div>剩余寿命</div>
              </div>
              <div>
                <span style="font-size:20px">32</span>
                天
                <span style="font-size:20px">15</span>
                小时
              </div>
              <div style="color:#aaa">
                预测时间：2022-05-30 12:00:05
              </div>
            </div>
          </div>
        </div>
        <div class="infoBox">
          <div class="title">运维决策</div>
          <div class="box">
            <div class="line">
              <div class="item" style="width:fit-content">
                <div>DPF清灰策略：当前需要进行清灰，可节约1999元综合成本</div>
              </div>
              <div style="color:#aaa">
                提醒时间：2022-05-30 12:00:05
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">运行概况</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import {getFaultList, getFaultDetail} from '@/api/suanpanLink'
import tabListVue from '../../export-data/tab-list.vue'
import moment from 'moment'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        type: '',
        name: '',
        code: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '故障树ID',
          key: 'id',
          align: 'left'
        },
        {
          title: '故障树名称',
          key: 'ft_name',
          align: 'left'
        },
        {
          title: '当前状态',
          key: 'status',
          align: 'left',
          render: (h, params) => {
            console.log(params.row.status)
            return h('div', {
              style: {
                color: params.row.status === '正常' ? 'green' : 'red'
              }
            }, params.row.status || '故障')
          }
        },
        {
          title: '诊断时间',
          key: 'update_time',
          align: 'left',
          render: (h, params) => {
            return h('div', moment(params.row.update_time).format('YYYY-MM-DD HH:mm:ss'))
          }
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.goDetail(params.row)
                    }
                  }
                },
                '查看详情'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      type: '',
      infoData: [],
      timer: null
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getlocation (e) {
      this.formValue.longitude = e.lng
      this.formValue.latitude = e.lat
    },
    getType () {},
    async loadTableData (clear) {
      this.pageInfo.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      await this.getFaultList()
      await this.loadStatus()
    },
    getFaultList () {
      return new Promise((resolve, reject) => {
        getFaultList().then(res => {
          let {code, data} = res
          if (code === 0) {
            this.infoData = data
            this.pageInfo.totalElements = data.length
            resolve()
          }
        })
      })
    },
    async loadStatus () {
      return new Promise((resolve, reject) => {
        for (let i in this.infoData) {
          let item = this.infoData[i]
          getFaultDetail({id: item.id}).then(res => {
            if (res.code === 0) {
              if (res.data.length !== 0) {
                console.log('1')
                item.status = '故障'
              } else {
                console.log('2')
                item.status = '正常'
              }
            } else {
              console.log('3')
              item.status = '正常'
            }
            if (Number(i) === this.infoData.length - 1) {
              this.tableData = this.infoData
              this.pageInfo.loading = false
            }
            resolve()
          })
        }
      })
    },
    goDetail (params) {
      this.$router.push({
        path: '/fault-detail',
        query: {id: params.id, picUrl: params.ft_base64, deviceName: params.ft_name, type: 1}
      })
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    }
  },
  destroyed () {
    clearInterval(this.timer)
  },
  mounted () {
    this.loadTableData()
    this.timer = setInterval(() => {
      this.loadTableData()
    }, 10000)
  }
}
</script>

<style lang="less" scoped>
@import '../../../style/varient';
@size-padding: 10px;
.top-box{
  display: flex;
  margin-bottom: 10px;
  .infoBox{
  width:49%;
  &:first-child{
    margin-right: 2%;
  }
  .title{

  }
  .box{
    margin-top: 15px;
    padding: 30px;
    border: 1px solid #515661;
    min-height: 200px;
    .line{
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      align-items: center;
      .item{
        text-align: center;
        width: 23%;
        font-size: 16px;
      }
    }
  }
}
}

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



